*,
*::before,
*::after {
  box-sizing: border-box;
}

ul[class],
ol[class] {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  color: #ff851b;
  background-color: #111111;
  font-family: "Ubuntu", sans-serif;
  font-weight: 300;
  font-size: 16px;
  overflow: hidden;
}

ul[class],
ol[class] {
  list-style: none;
}

a:not([class]) {
  color: #ff851b;
  text-decoration: none;
  text-decoration-skip-ink: auto;
}

img {
  max-width: 100%;
  display: block;
}

[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "social";
  font-style: normal;
  font-weight: normal;
  speak: never;

  display: inline-block;
  text-decoration: inherit;
  width: 1em;
  margin-right: 0.2em;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  line-height: 1em;
  margin-left: 0.2em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-facebook:before {
  content: "\e800";
}
.icon-linkedin:before {
  content: "\e803";
}
.icon-github:before {
  content: "\f056";
}
.icon-instagram:before {
  content: "\f16d";
}
.icon-mail:before {
  content: "\e802";
}

.icon-share:before {
  content: "\e801";
}
.icon-file-pdf:before {
  content: "\f1c1";
}

.container > *,
.networks,
article,
article:first-child > *,
article:first-child > * > i::before {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
}

.container {
  margin-right: auto;
  margin-left: auto;
  height: 100vh;
}

.container > * {
  align-items: center;
}

section {
  height: 80vh;
  justify-content: center;
  flex-direction: column;
}

article {
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
}

article > * {
  margin: 3vh;
}

article:first-child > a,
article:first-child > a > i::before {
  justify-content: center;
  align-items: center;
}

article:first-child > a {
  border: 1px solid #ff851b;
  border-radius: 50%;
  width: 15vh;
  height: 15vh;
}

article:first-child > a:hover {
  background-color: #ff851b;
  color: #111111;
}
article:first-child i {
  font-size: 7vmin;
}

article:nth-child(2) > span {
  opacity: 0.5;
}

header,
footer {
  height: 10vh;
  padding: 0 15px;
}

header {
  font-size: 1.2em;
}

footer {
  justify-content: flex-end;
}

@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

@media only screen and (min-width: 48em) {
  .container {
    width: 49rem;
  }
}

@media only screen and (min-width: 64em) {
  .container {
    width: 65rem;
  }
}

@media only screen and (min-width: 75em) {
  .container {
    width: 76rem;
  }
}
